import React from 'react';
import { useTranslation } from 'react-i18next';
import { Helmet } from 'react-helmet';

import './index.scss';
import {
    dishesList,
    advantageList,
    bonusList
} from './list';
import HeadingBlock from '../../components/HeadingBlock';

const Delivery = (props) => {
    const { t } = useTranslation();

    const createJSONLD = () => {
        const data = {
            '@context': 'https://schema.org/',
            "@type": "Offer",
            "areaServed": 'Днепр, Запорожье',
            "description": t('delivery.left.1') + t('delivery.left.2') + t('delivery.left.3') + t('delivery.right.1') + t('delivery.right.2'),
            "seller": {
                "@type": 'Organization',
                "name": '24ROLLS'
            }
        };

        return JSON.stringify(data);
    }

    return (
        <div className={'delivery'}>
            <Helmet>
                <title>
                    {props.seo ? props.seo.title : '24ROLLS'}
                </title>
                <meta name="description" content={props.seo && props.seo.description} />
                <meta name="keywords" content={props.seo && props.seo.keywords} />
                <script async type="application/ld+json">
                    {createJSONLD()}
                </script>
                <script type="application/ld+json">
                    {JSON.stringify({ "@context": "http://schema.org", "@type": "WebSite", "@id": "#website", "url": "https://24rolls.zp.ua/", "name": "\u0421\u0443\u0448\u0438 24Rolls Dnepr", "potentialAction": { "@type": "SearchAction", "target": "https://24rolls.zp.ua/?s={search_term_string}", "query-input": "required name=search_term_string" } })}
                </script>
            </Helmet>

            <HeadingBlock
                title={'Доставка'}
                svg={true}
            />
            <div className={'description'}>
                <div className={'description_block-head'}>
                    <h3>
                        {t('delivery.button.1')}
                    </h3>
                </div>
            </div>
            <div className={'mainText'}>
                <div className={'mainText_left'}>
                    <p>
                        {t('delivery.left.1')}<br />
                        {t('delivery.left.2')}
                    </p>
                    <p>
                        {t('delivery.left.3')}
                    </p>
                </div>
                <div className={'mainText_right'}>
                    <p>
                        {t('delivery.right.1')}
                    </p>
                    <p>
                        {t('delivery.right.2')}
                    </p>
                </div>
            </div>
            <div className={'dishes'}>
                <div className={'dishes_content'}>
                    <p>
                        {t('delivery.dishes.title.1')}<br />
                        <strong>
                            {t('delivery.dishes.title.2')}
                        </strong>
                    </p>
                    {
                        dishesList.map(el =>
                            <li key={el.id}>
                                {t(`${el.name}`)}
                            </li>
                        )
                    }
                </div>
            </div>
            <div className={'description'}>
                <div className={'description_block-head'}>
                    <h3>
                        {t('delivery.button.2')}
                    </h3>
                </div>
            </div>
            <div className={'advantage'}>
                <div className={'advantage_number'}>
                    {
                        advantageList.map(el =>
                            <li
                                key={el.id}
                                className={'advantage_number_block'}
                            >
                                <h2>{el.title}</h2>
                                <div className={'advantage_number_block_list'}>
                                    <img
                                        src={el.image}
                                        alt=""
                                    />
                                    <p>{t(`${el.text}`)}</p>
                                </div>
                            </li>
                        )
                    }
                </div>
            </div>
            <div className={'description'}>
                <div className={'description_block-head'}>
                    <h3>
                        {t('delivery.button.3')}
                    </h3>
                </div>
            </div>
            <div className={'bonus'}>
                <div className={'bonus_title'}>
                    <h2>
                        {t('delivery.presents.title')}
                    </h2>
                </div>
                <div className={'bonus_rolls'}>
                    {
                        bonusList.map(el =>
                            <li
                                className={'bonus_rolls_block'}
                                key={el.id}
                                style={{ backgroundImage: `url(${el.image})` }}
                            >
                                <div className={'bonus_rolls_block_list'}>
                                    <h2>{t(`${el.title}`)}</h2>
                                    <p>{t(`${el.text}`)}</p>
                                </div>
                            </li>
                        )
                    }
                </div>
            </div>
            <div className={'description'}>
                <div className={'description_block-head'}>
                    <h3>
                        Зона доставки
                    </h3>
                </div>
            </div>
            <div className={'footer'}>
                <div className={'footer_inform'}>
                    <p>
                        {t('delivery.presents.area')}
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Delivery;